@import "../../../assets/css/mixins";

.tabs {
  &_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    &:after {
      position: absolute;
      content: '';
      left: 0;
      bottom: 0;
      width: 630px;
      height: 1px;
      background: linear-gradient(90deg, #6C6686 0%, #0A0118 100%);
      @include mobile-only {
        width: 260px;
      }
    }
  }
  &_list {
    display: flex;
    align-items: center;
  }
  &_item {
    position: relative;
    margin-right: 50px;
    padding: 0 0 9px 0;
    color: #A29CBC; // 灰色
    background: transparent;
    cursor: pointer;
    transition: color 0.2s;
    font-family: Poppins;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    border: none;
    &:last-child {
      margin-right: 0;
    }
  @include mobile-only {
    font-size: 16px;
    line-height: 24px;
    margin-right: 20px;
  }

    &:hover {
      color: #ffffff; // hover 亮一点
    }

    &.active {
      color: #ffffff;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 4px;
        background: linear-gradient(90deg, #6E0DFF 0%, rgba(110, 13, 255, 0.1) 100%);
      }
    }
  }
  &_right {
    display: flex;
    align-items: center;
    @include mobile-only {
      padding-bottom: 9px;
    }
  }
}

