@import "../../assets/css/mixins";
.label {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #A29CBC;
}
.num {
  font-weight: 700;
  font-size: 20px;
  color: #fff;
}
.warp {
  margin: 30px 0 40px;
  display:  flex;
  align-items: center;
  gap: 10px;
  @include mobile-only {
    padding: 0 15px;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px 10px;
    margin: 12px 0 16px;
  }
  &_balance {
    display:  flex;
    align-items: center;
    gap: 10px;
  }
}
.pool_setting {
  font-weight: 400;
  color: #A29CBC;
  cursor: pointer;
  margin-left: auto;
  font-family: Poppins;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  display: flex;
  align-items: center;
  @include mobile-only {
    margin-left: 0;
  }
}
.pool_setting :global(.ant-switch) {
  background: #fff;
  position: relative;
  border-radius: 34px;
  overflow: hidden; /* 保证内容不会溢出 */
  height: 16px;
}
.pool_setting :global(.ant-switch:before) {
  content: '';
  position: absolute;
  inset: 0;
  padding: 0.5px; /* 边框宽度 */
  border-radius: 34px;
  background: linear-gradient(180deg, #a986f6, rgba(169, 134, 246, 0.3));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  pointer-events: none; /* 不影响内部点击 */
}
.pool_setting :global(.ant-switch.ant-switch-checked) {
  background: #6E0DFF;
}
.pool_setting :global(.ant-switch .ant-switch-inner) {
  //background: #6E0DFF;
  border-radius: 34px;
  padding: 0 2px;
}
.pool_setting :global(.ant-switch .ant-switch-handle) {
  width: 12px;
  height: 12px;
  top: 1px;
}
.pool_setting :global(.ant-switch .ant-switch-handle:before) {
  background: linear-gradient(148.24deg, #986CF1 12.5%, #DBCAFF 87.25%);
  border: 0.5px solid #E0D0FF
}
.pool_setting :global(.ant-switch.ant-switch-checked .ant-switch-handle) {
  left: calc(100% - 14px);
}