@import "../../assets/css/mixins";
.rewards {
  background: #1B0D31;
  border-radius: 10px;
  padding: 20px;
  //margin-top: -20px;
  display:  flex;
  align-items: center;
  @include mobile-only {
    display: block;
    background: #26193D;
    padding: 20px 0 0 0;
  }
}
.label {
  color: #A29CBC;
  font-weight: 400;
  font-size: 12px;
  margin-right: 6px;
  line-height: 18px;
}
.num_big {
  font-family: Poppins;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #fff;
  @include mobile-only {
    font-family: Poppins-Blod;
  }
}
.num_medium {
  font-family: Poppins;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
}

.card {
  background: #26193D;
  width: 240px;
  height: 66px;
  border-radius: 10px;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include mobile-only {
    background: transparent;
    border-radius: 0;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: auto;
    padding: 0;
  }
  &_symbol {
    display:  flex;
    align-items: center;
    & img {
      margin-right: 4px;
    }
  }
  &_percent {
    height: 20px;
    line-height: 20px;
    padding:  0 4px;
    border-radius: 4px;
    background: rgba(#fff, .1);
    color: #A7A2C9;
    margin-left: 10px;
    font-size: 12px;
  }
}
.card_wrap {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  @include mobile-only {
    background: #33254E;
    border-radius: 10px;
    flex-direction: column;
    margin-top: 10px;
    margin-bottom: 20px;
    padding: 10px;
    gap: 16px;
  }
}
.collect_btn {
  margin-left: auto;
  margin-top: 30px;
}
.liquidity {
  margin-right: 60px;
  @include mobile-only {
    margin-right: 0;
  }
}
.reward_info {
  text-align: center;
}
.amount {
  font-family: Poppins;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #fff;
  @include mobile-only {
    font-size: 20px;
    line-height: 20px;
  }
}
.plus {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: rgb(160, 156, 172);
  margin: 0 10px;
}
.token_symbol {
  font-family: Poppins;
  font-weight: 700;
  font-size: 14px;
  line-height: 14px;
  color: #fff;
  margin-left: 6px;
}
.modal_footer {
  margin-top: 30px;
}
.error {
  color: #FF8700;
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}
.success_img {
  width: 80px;
}
.end_tip {
  color: #6C6686;
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  margin-top: 20px;
  line-height: 18px;
}
.reward_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@include mobile-only {
  .rewards_box {
    .card_wrap {
      margin-bottom: 0;
    }
  }
}