@import "../../../assets/css/mixins";

.capsule {
  &_list {
    display: flex;
    align-items: flex-start;
  }
  &_item {
    height: 30px;
    line-height: 30px;
    padding: 0 12px;
    font-weight: 600;
    font-size: 16px;
    color: #A29CBC;
    cursor: pointer;
    transition: all 0.2s ease;
    margin-right: 10px;
    border-radius: 24px;
    &:last-child {
      margin-right: 0;
    }
    @include mobile-only {
      height: 28px;
      line-height: 28px;
      padding: 0 10px;
      font-size: 14px;
    }
    &.active {
      color: #ffffff;
      background: #6400FF;
      cursor: default;
      @include mobile-only {
        border-radius: 18px;
      }
    }
    @include pc-only {
      &:hover {
        color: #ffffff;
        background: #36254E;
      }
    }
  }
}