@import "../../../assets/css/_mixins";

.base_button {
  // 清除默认样式
  appearance: none;
  background: transparent;
  border: none;
  margin: 0;
  padding: 0;

  // 继承文字样式
  font: inherit;
  color: inherit;
  line-height: normal;

  // 鼠标手势
  cursor: pointer;

  // 默认radius
  border-radius: 6px;
  // 移除 focus outline，留给后续自定义 focus 样式
  &:focus {
    outline: none;
  }
  &:disabled {
    cursor: no-drop;
    opacity: 0.5;
  }
}
.button_type {
  &_secondary {
    background-color: #33254E;
    color: #fff;
    @include pc-only {
      &:hover:not(:disabled) {
        background-color: #6400FF;
        box-shadow: 0px -4px 0px 0px #09011733 inset;
      }
    }
    @include mobile-only {
      &:active:not(:disabled) {
        background-color: #6400FF;
        box-shadow: 0px -4px 0px 0px #09011733 inset;
      }
    }
  }
  &_warning {
    background-color: #FF8700;
    color: #fff;
    @include pc-only {
      &:hover:not(:disabled) {
        opacity: 0.7;
      }
    }
    @include mobile-only {
      &:active:not(:disabled) {
        opacity: 0.7;
      }
    }
  }
  &_primary, &_primary_linear {
    background-color: #6400FF;
    color: #fff;
  }
  &_primary_linear:hover {
    background: linear-gradient(90.44deg, #6577FF 0%, #6400FF 99.25%);
    box-shadow: 0px -4px 0px 0px #0901174D inset;
  }
  &_g_host {
    background-color: transparent;
    color: #A29CBC;
    border: 1px solid #A29CBC;
    @include pc-only {
      &:hover {
        color: #fff;
        border-color: #fff;
      }
    }
    @include mobile-only {
      color: #fff;
      border-color: #fff;
    }
  }
}
.button_type {
  &_36 {
    height: 36px;
    line-height: 36px;
    font-weight: 600;
    font-size: 14px;
    @include mobile-only {
      height: 30px;
      line-height: 30px;
      border-radius: 4px;
    }
  }
  &_36_26 {
    height: 36px;
    line-height: 36px;
    font-weight: 600;
    font-size: 14px;
    @include mobile-only {
      height: 26px;
      line-height: 26px;
      border-radius: 4px;
      font-size: 12px;
    }
  }
  &_36_34 {
    height: 36px;
    line-height: 36px;
    font-weight: 600;
    font-size: 14px;
    @include mobile-only {
      height: 34px;
      line-height: 34px;
      border-radius: 4px;
    }
  }
  &_56 {
    height: 56px;
    line-height: 56px;
    font-weight: 600;
    font-size: 20px;
    border-radius: 10px;
    @include mobile-only {
      height: 40px;
      line-height: 40px;
      border-radius: 4px;
      font-size: 14px;
    }
  }
}
.button_padding_h {
  &_10 {
    padding: 0 10px;
  }
  &_16 {
    padding: 0 16px;
    @include mobile-only {
      padding: 0 8px;
    }
  }
}
.button_blocked {
  width:  100%;
  display: block;
}