@import "../../assets/css/mixins";

$color-text-sub: #A29CBC;
$color-text-label: #A7A2C9;
$color-up: #00FF96;
$color-down: #FF3287;

.pools_page {
  //background-image: url(../../assets/images/pool/pools_bg.svg);
  background-position: calc(50% - 300px) -10px;
  background-repeat: no-repeat;
  @include mobile-only {
    background-image: url(../../assets/images/pool/pools_bg_m.svg);
    background-repeat: no-repeat;
    background-position: left -50px;
    //padding: 0 15px;
  }
}
.wrap {
  width:  100%;
  margin:0 auto;
  max-width: 1200px;
  //background-image: url(../../assets/images/pool/pools_bg_right.png);
  background-repeat:  no-repeat;
  background-position: right -30px top -30px;
  background-size: 558px 437px;
  @include  mobile-only {
    background: none;
  }
}
.pools_bg {
  position: absolute;
  left: -150px;
  top: -120px;
  z-index: 2;
}
.pools_video {
  position: absolute;
  right: -30px;
  top: -30px;
  z-index: 1;
  width: 588px;
  height: 438px;
  border: 0;
  background: transparent;
}

.pools {
  &_head {
    padding-top: 70px;
    margin-bottom: 80px;
    font-family: Poppins;
    position: relative;
    @include mobile-only {
      padding-top: 10px;
      margin-bottom: 40px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  &_title {
    height: 90px;
    @include mobile-only {
      height: 45px;
    }
  }
  &_sub {
    color: $color-text-sub;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    margin-top: 10px;
    margin-bottom: 50px;
    @include mobile-only {
      font-size: 16px;
      margin-bottom: 30px;
    }
  }
  &_data {
    display:  flex;
    gap: 60px;
    @include mobile-only {
      gap: 40px;
    }
    &_info {}
  }
  &_label {
    color: $color-text-label;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 10px;
  }
  &_num {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    color: #fff;
    @include mobile-only {
      font-size: 20px;
      line-height: 30px;
    }
  }
  &_percent {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    margin-left: 10px;
    @include mobile-only {
      display: block;
      margin-left: 0;
      margin-top: 2px;
    }
    &_up {
      color: $color-up;
    }
    &_down {
      color: $color-down;
    }
  }
}

@include mobile-only {
    .pool_tab {
      //position: sticky;
      //top: 104px;
      //z-index: 2;
      padding: 0 15px;

      &_sticky {
        backdrop-filter: blur(20px);
        background: rgba(11, 5, 25, 0.5019607843);
      }
    }
}

.filed_with_tip {
  display: flex;
  align-items:  center;
  gap: 6px;
}