.switch_wrapper {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 30px;
  height: 16px;
  position: relative;
  &_close {
    background-image: url("../../../assets/images/switch/outer_close.png");
  }
  &_open {
    background-image: url("../../../assets/images/switch/outer_open.png");
  }
}
.thumb {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 12px;
  height: 12px;
  position: absolute;
  left: 3px;
  top: 2px;
  transition: all 400ms ease;
}
.switch_wrapper_close {
  .thumb {
    background-image: url("../../../assets/images/switch/inner_close.png");
  }
}
.switch_wrapper_open {
  .thumb {
    background-image: url("../../../assets/images/switch/inner_open.png");
    left: 16px;
  }
}
