.ancher {
  display: flex;
  font-size: 12px;
  align-items: center;
  &_text {
    margin: 0 10px 0 6px;
  }
  &_prefix {
    width: 12px;
  }
  &_suffix {
    width: 12px;
  }
}
.sorter {
  display:  flex;
  align-items: center;
  justify-content: space-between;
  cursor:  pointer;
  white-space: nowrap;
  &_active {
    color: #C1A0FF;
  }
}