@import "../../../assets/css/mixins";

.card {
  background: #26193D;
  border: 1px solid #33254E;
  border-radius: 10px;
  padding: 10px;
  color: #fff;
  margin-bottom: 10px;
}
.item_line {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-family: Poppins;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 16px;
  @include mobile-only {
    margin-bottom: 12px;
  }
}
.label {
  color: #6C6686;
  margin-right: auto;
}
.center {
  color: #A29CBC;
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  margin-right: 4px;
}
.text {
  font-size: 14px;
  line-height: 22px;
  color: #FFFFFF;
}
.view_more {
  text-align: center;
  margin-top: 20px;
  //margin: 20px 0 0 50%;
  //transform: translateX(-25%);
  //display:  inline-flex;
  //justify-content: center;
  //align-items: center;
  //color: #BFABFF;
  //gap: 6px;
  //cursor: pointer;
}
.info {
  background: #33254E;
  border-radius: 6px;
  padding: 10px;
  margin-top: 12px;
  &_line {
    display:  flex;
    justify-content: flex-start;
    align-items:  center;
  }
  &_label {
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #A29CBC;
  }
  &_text {
    font-family: Poppins;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    color: #FFFFFF;
    margin-left: auto;
  }
  &_icon {
    height: 14px;
    margin-right: 6px;
  }
}
.btns_1 {
  display:  flex;
  gap: 15px;
  button {
    flex: 1
  }
}
.btns_2 {
  display:  flex;
  gap: 10px;
  button {
    flex: 1
  }
  .expend {
    flex: 0 0 30px; /* 固定宽度30px */
  }
}
.info_2 {
  display: flex;
  background: #33254E;
  border-radius: 6px;
  position: relative;
  padding: 10px 0;
  margin-top: 12px;
  &:after {
    content: '';
    position: absolute;
    width: 1px;
    height: 40px;
    background: #443564;
    left: 50%;
    transform: translateX(-25%);
  }
  &_item {
    flex: 1;
    text-align: center;
  }
  &_label {
    font-family: Poppins;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: #A29CBC;
    margin-bottom: 6px;
  }
  &_num {
    font-family: Poppins;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    color: #FFFFFF;
  }
}