@import "../../assets/css/mixins";
.cell {
  display: flex;
  align-items: center;
  gap: 10px;
  //padding-left: 10px;
  //@include mobile-only {
  //  padding-left: 0;
  //}
}

.name {
  font-weight: 500;
  font-size: 14px;
  @include mobile-only {
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
    display: block;
    margin-bottom: 6px;
  }
}
.tags {
  display: flex;
  align-items:  center;
}
.icon {
  flex-shrink: 0;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  @include mobile-only {
    height: 50px;
    width: 50px;
  }
  &_curve {
    border-radius: 0;
    width: auto;
    @include mobile-only {
      height: 24px;
      width: auto;
    }
  }
}
.pool_v1v2 {
  .icon {
    @include mobile-only {
      width: 24px;
      height: 24px;
    }
  }
}
.iconGroup {
  display: flex;
  align-items: center;
  position: relative;
}

.iconOverlap {
  margin-left: -4px; // 两个图标重叠
  @include mobile-only {
    margin-left: -10px;
  }
}

.empty {
  color: #999;
}
.rate {
  background: #26193D;
  height: 20px;
  line-height: 20px;
  border-radius: 11px;
  padding: 0 6px;
  font-family: Poppins;
  font-weight: 600;
  font-style: SemiBold;
  font-size: 12px;
  color: #A29CBC;
  @include mobile-only {
    height: 20px;
    border-radius: 11px;
    padding: 0 6px;
    line-height: 20px;
    color: #A29CBC;
    font-weight: 600;
    font-size: 12px;
    display: inline-block;
    background: #33254E;
    margin-right: 10px;
  }
}
.version {
  font-family: Poppins;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  background: #986CF14D;
  border: 1px solid #C1A0FF80;
  border-radius: 20px;
  padding: 0 6px;
  height: 20px;
  color: #C1A0FF;
}