@import "../../../assets/css/mixins";

// 自定义类名，覆盖变量即可
.table_v2 {
  --table-width: 1200px;
  --table-hover-padding: 10px;
  --table-hover-bg: #191227;
  --table-hover-radius: 6px;
}
.table_v2 :global(.ant-table-container .ant-table-tbody > tr > td),
.table_v2 :global(.ant-table-thead >tr>th) {
  border-bottom: none;
}
.table_v2 :global(.ant-table-thead >tr>th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before) {
  display:  none;
}
.table_v2 :global(.ant-table-thead>tr>th) {
  padding: 0 0 18px 0;
}
.table_v2 :global(.ant-table-thead>tr>th .ant-table-column-sorters) {
  display: inline-flex;
}
.table_v2 :global(.ant-table-tbody>tr>td) {
  padding: 12px 0 42px 0;
}

.table_v2 :global(.ant-table-tbody>tr.ant-table-expanded-row>td) {
  padding: 0 0 30px 0;
}
.table_v2 :global(.ant-table-tbody .v3-expended-row td) {
  padding-bottom: 20px;
}

.table_v2 td {
  vertical-align: middle;
}
.table_v2 :global(.ant-table-tbody tr.ant-table-row) {
  cursor: pointer;
  position: relative;

  &:hover td:first-child::before {
    position: absolute;
    content: "";
    left: calc(-1 * var(--table-hover-padding));
    width: calc(var(--table-width) + var(--table-hover-padding) + var(--table-hover-padding));
    background: var(--table-hover-bg);
    display:  block;
    top: 0;
    bottom: 30px;
    z-index: -1;
    border-radius: var(--table-hover-radius);
  }
}
.table_v2 :global(.ant-table-tbody tr.v3-expended-row) {
  &:hover td:first-child::before {
    position: absolute;
    content: "";
    left: calc(-1 * var(--table-hover-padding));
    width: calc(var(--table-width) + var(--table-hover-padding) + var(--table-hover-padding));
    background: var(--table-hover-bg);
    display:  block;
    top: 0;
    bottom: 8px;
    z-index: -1;
    border-radius: var(--table-hover-radius);
  }
}
