@import "../../assets/css/mixins";

.search {
  display:  flex;
  align-items:  center;
  justify-content: space-between;
  margin: 30px 0;
  @include mobile-only {
    display: block;
    padding: 0 15px;
    //position: sticky;
    //top: 137px;
    padding-top: 12px;
    z-index: 1;
    padding-bottom: 10px;
    margin: 0;
  }
  &_sticky {
    backdrop-filter: blur(20px);
    background: rgba(11, 5, 25, 0.5019607843);
  }
  .input_wrap {
    margin-top: 10px;
    display: flex;
    gap: 10px;
  }
  input {
    width: 823px;
    @include mobile-only {
      width: auto;
      height: 30px;
      font-size: 12px;
    }
  }
}