@import "../../../assets/css/mixins";

.common-connect-box-v2 {
  &-wrap {
    background: transparent;
    color: #A29CBC;
    text-align: center;
  }
  &-img {
    width: 280px;
    height: 240px;
    margin-bottom: 10px;
    margin-top: 50px;
    @include mobile-only {
      width: 196px;
      height: auto;
      margin-top: 40px;
      margin-bottom: 0;
    }
  }
  &-text {
    font-size: 12px;
    color: #A29CBC;
    font-family: Poppins;
    font-weight: 400;
  }
  &-btn {
    margin-top: 20px;
    display: inline-block;
  }
}