@import '../../../assets/css/var.scss';
@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.setting_wrap {
  display: flex;
  align-items: center;
  margin-right: 10px;
  white-space: nowrap;

  :global {
    .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled) {
      &:focus,
      &:hover {
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.1);
        box-shadow: none;
      }
      .ant-input-suffix {
        color: rgba(167, 162, 201, 1);
      }
    }
  }
}
.setting_item_line {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
  @include mobile-only {
    margin-bottom: 16px;
  }
  &:last-child {
    margin-bottom: 0;
  }
  & > div:first-child {
    flex: 0 0 100%;
  }
}

.setting_label {
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: #fff;
  @include mobile-only {
    font-size: 14px;
  }
}

.setting_btn_wrap {
  display: flex;
  //overflow: hidden;
  border-radius: 4px;
  background:#33254E;
  height: 30px;
  flex: 0 0 auto
}
.setting_input_slippage {
  width: 90px;
}
.setting_item {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  background: transparent;
  font-size: 14px;
  font-family: Poppins;
  font-weight: 400;
  color: #fff;
  cursor: pointer;
  height: 30px;
  line-height: 21px;
  border-radius: 4px;
  width: 55px;
}
.active {
  background: #6400FF;
  color: #fff;
}

.setting_input {
  height: 30px;
}

.module_important {
  background: rgba(50, 35, 83, 1);
  color: #fff;
  width: 100%;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 3px 11px;
  height: 30px;
  &:focus,
  &:hover {
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: none;
  }
  input {
    background: transparent;
    padding: 5px 10px;
    font-size: 14px;
    font-family: Poppins;
    font-weight: 400;
    color: #fff;
    opacity: 0.7;
  }
}

.info_text {
  color: #ffc400;
  margin-bottom: 10px;
}

.setting_minutes {
  font-size: 14px;
  font-family: Poppins;
  font-weight: 400;
  color: rgba(167, 162, 201, 1);
  margin-left: 10px;
}

.flex_right {
  display: flex;
  align-items: center;
  gap: 10px;
  :global {
    .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled) {
      &:focus,
      &:hover {
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.1);
        box-shadow: none;
      }
      .ant-input-suffix {
        color: rgba(167, 162, 201, 1);
      }
    }
  }
}
.minius_text {
  color: #9E9ABC;
}