@import "../../assets/css/mixins";

$gray-color: #9E9ABC;

.gray {
  color: $gray-color;
}
.font_weight {
  &_600 {
    font-weight: 600;
  }
}
.font {
  &_12 {
   font-size: 12px;
  }
}
.mb {
  &_10 {
    margin-bottom: 10px;
  }
}
.migrate {
  font-size: 14px;
  color: #fff;
  @include mobile-only {
    font-size: 12px;
  }
}
:global(#root) p.migrate_desc {
  color: $gray-color;
  margin-bottom: 30px;
  text-align: left;
  @include mobile-only {
    margin-bottom: 20px;
  }
}
.lp {
  display:  flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 12px;
  flex-wrap: wrap;
}
.lp_right {
  margin-left: auto;
  @include mobile-only {
    flex-basis: 100%;
    margin-top: 6px;
  }
}
.logo {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  &_right {
    margin-left: -4px;
    margin-right: 10px;
  }
}
.token_name {
  font-size: 14px;
  font-family: Poppins, serif;
  font-weight: 600;
  color: #fff;
  &_14_12 {
    font-size: 14px;
    @include mobile-only {
      font-size: 12px;
    }
  }
  &_medium {
    margin: 0 10px;
    @include mobile-only {
      font-size: 12px;
      margin: 0 10px 0 4px;
    }
  }
  &_big {
    font-size: 16px;
    font-weight: 700;
  }
  &_regular {
    font-weight: 400;
  }
  &_gray {
    color: #9E9ABC;
  }
}
.version, .version_big {
  color: #6400FF;
  width: 30px;
  height: 17px;
  line-height: 17px;
  background: #C1A0FF;
  border-radius: 10px;
  text-align: center;
  display: inline-block;
  font-size: 12px;
  font-weight: 600;
  margin-left: 10px;
}
.version_big {
  width: 38px;
  height: 26px;
  line-height: 26px;
  font-size: 16px;
  border-radius: 15px;
  margin: 0;
}
.info {
  background: #33254E;
  padding: 20px;
  margin-bottom: 10px;
  @include mobile-only {
    padding: 10px;
  }
}
.info :global(.info_box_item) {
  margin-bottom: 14px;
  @include mobile-only {
    margin-bottom: 10px;
  }
}
.info :global(.info_box_item_label) {
  display: inline-flex;
  align-items: center;
}
.info :global(.info_box_item_label) img {
  margin-right: 6px;
}
.info :global(.info_box_item_value) {
  font-weight: 600;
  font-size: 14px;
}
.rate {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.old_rate {
  margin-left: auto;
}
.swap_icon {
  width: 22px;
  height: 22px;
  display: inline-block;
  border-radius: 50%;
  line-height: 22px;
  text-align: center;
  background: #36254E;
}

.migrate_line {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  &_dash {
    height: 9px;
  }
}
.amount {
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
}
.symbol {
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  margin-left: 6px;
}
.plus {
  margin: 0 10px;
  font-weight: 700;
  font-size: 20px;
  color: rgb(160, 156, 172);
  @include mobile-only {
    display: block;
  }
}
.transaction_desc {
  color: $gray-color;
  font-family: Poppins;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
}