.drop_down_v2:global(.ant-dropdown) {
  color: #fff;
}
.drop_down_v2 :global(.ant-dropdown-menu) {
  padding: 0;
  list-style-type: none;
  background: #26193D;
  border-radius: 8px;
  border: 1px solid #FFFFFF33;
  outline: none;
  box-shadow: 0px 0px 8px 0px #06010D4D;
}
.drop_down_v2 :global(.ant-dropdown-menu .ant-dropdown-menu-item) {
  padding: 0 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #FFFFFF;
}